@import 'Content_d.css';

/*only screen and (min-width: 1200px)*/

:root {

    /* Colors: */
    --diviWhite: #FFFFFF;
    --diviGrey: #6D6A6A;
    --diviSilver: #8B8989;
    --diviRed: #F81212;
    --diviBlue: #72A9C5;
    --diviDark: #36444B;
    --diviSky: #E5F6FF;
    --diviGreen: #83DE71;
    --diviBorder: #707070;
}

.wrapper {
    width: 100%;
}

@media screen and (min-resolution: 105.6dpi)
{
    .zoomWrapper {
        transform: scale(0.9);
        transform-origin: -3rem 9rem;
        width: 125%;
        height: 125%;
    }

    .sidebar-container {
        transform: scale(0.9);
        transform-origin: top left;
        width: 125%;
        height: 125%;
    }

    .sidebar {
        margin-top: 5rem;
        transform: scale(0.9);
        transform-origin: top left;
        height: 125%;
    }

    div.vertical {
        transform: scale(0.9);
        transform-origin: -30em -5em;
        height: 100%;
    }
}

@media screen and (min-resolution: 120dpi)
{
    .zoomWrapper {
        transform: scale(0.8);
        transform-origin: -2rem 0;
        width: 125%;
        height: 125%;
    }
